:root {
  --primary: #1e7ac6;
  --grey: #f6f6f6;
  --header-background: rgba(255, 255, 255, 0.8);
}

.container {
  max-width:1400px;
  margin:0 auto;
  display:flex;
  align-items:center;
  height:100%;
  padding:0 40px;
}

.header {
  z-index:999;
  background-color: var(--header-background);
  width:100%;
  height:66px;
  position:sticky;
  top:0;
  font-size:1rem;
}

.app-body {

}

.app-name {
  color: var(--primary);
  font-weight:900;
  font-size:1.3rem;
}

.navigation-container {
  width:100%;
}

.navigation-login-container {
  display:flex;
  flex-grow:1;
  justify-content: flex-end;
}

.navigation-list ul, .navigation-list li {
  list-style-type:none;
  display:inline-block;
  margin:0 20px;
  font-weight:bold;
}

a {
  text-decoration: none;
  color:inherit;
  transition: color 0.3s;
  font-weight:bold;
}

a:hover {
  color:var(--primary);
}

.row {
  display:flex;
  flex-direction:row;
  align-items: center;
}

.cta-btn {
  background-color: var(--primary);
  color:white;
  border-radius:40px;
  padding: 15px 40px;
  font-weight:bold;
  border:none;
  font-size:0.9rem;
  letter-spacing: 1.5px;
  transition: opacity 0.3s;
}

.cta-btn:hover {
  cursor:pointer;
  opacity:0.8;
}

.hero {
  height:550px;
  width:100%;
  display:flex;
  align-items: center;
  justify-content: center;
  font-size:3rem;
  position:relative;
}

.hero-image {
  width:100vw;
  height:100%;
  position: absolute;
  top:0;
  left:0;
  background: url('https://wallpaperboat.com/wp-content/uploads/2020/06/03/42361/aesthetic-anime-01.jpg');
  filter: blur(50px);
  overflow:none;

}

.hero-text {
  display:flex;
  flex-direction: column;
  z-index:9999;
}

.featured-content {
  padding:60px;
}

.section-header {
  font-size:1.8rem;
  font-weight: 700;
  font-family: "Varela Round";
  letter-spacing: 1.5px;
  margin-bottom:40px;
}

.w100 {
  width: 100%;
}

.text-light {
  color:#666;
}

.content-list ul, .content-list li {
  display:inline-block;
}

.content-list {
  display:flex;
  justify-content: center;
}

.content-list li {
  margin:0 15px;
}

.grey-section, .white-section, .primary-section {
  padding: 20px 0;
}

.grey-section {
  padding-bottom:0;
}

.grey-section {
  background-color: var(--grey);
  min-height: 300px;
}

.white-section {
  background-color: white;
  min-height: 300px;
}

.primary-section {
  background-color: var(--primary);
  min-height: 300px;
}

.box-shadow {
  box-shadow: 0 13px 32px -7px rgba(6,6,22,.19);
}

.inline-list ul, .inline-list li {
  display:inline-block;
}

.inline-list {
  display:flex;
  justify-content: center;
}

.inline-list li {
  margin: 0 40px;
}

.card {
  border-radius:4px;
  padding:20px 40px;
}

.row > div {
  display:flex;
  flex:1;
}

.text-xxl {
  font-size:2.5rem;
}

.col {
  display:flex;
  flex-direction: column;
}

.content-description-view {
  opacity:0;
  background-color: rgba(30, 198, 119, 0.95);
  color:white;
  font-weight:400;
  padding:20px;
  position:absolute;
  top:0;
  width:220px;
  height:330px;
  transition: opacity 0.4s;
}

.text-primary {
  color: var(--primary);
}

.content-description-view:hover {
  opacity: 1;
}